.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.react-pdf__Page__canvas {

  width: 100% !important;
  height: 100% !important;
}
.react-pdf__Page__annotations{
  max-width: 350px;
  max-height: 553px;
}


.align-text{
  text-align: center;
}
.download-document {
  display:flex;
  align-items: center;
  justify-content: center;
}

.download-icon{
  margin-right: 10px;
  color: #032e1e;
}
.pdf-title{
  margin-bottom: 10px!important;
}
.MuiToolbar-root{
  padding-right: 0px!important;
  padding-left: 0px!important;
}
.theme-color{
  color:#032e1e;
}
.white-color{
  color:#fff;
}

.home-title{
  font-weight: 500!important;
  max-width: 300px!important;
  margin-top: 80px!important;

}

.home-text{
  max-width: 45%;
}
.home-image{
  max-width: 55%;
}
.home-subtitle{
  margin-left: 110px;
}
.margin-top20{
  margin-top: 20px!important;
}
.home-title-text{
  margin-top: 95px!important;
}

.footer-parent{
  max-width: 100%!important;
  padding-left: 0!important;
  padding-right: 0!important;
  background-color: #032e1e;
  padding-top: 10px;
  padding-bottom: 10px;
  margin-top: 70px;
  color: #fff;
}

.footer-box{
  width: 33%;
  display: flex;
  flex-direction: column;
}

.footer-container{
  display: flex!important;
  flex-wrap: wrap;
}

.footer-sub-box1{
  padding-bottom: 0;
}
.footer-sub-box2{
  align-items: center;
}

.footer-sub-box3{
  align-items: end;
}

.footer-address{
  margin-top: 10px;
  display: flex;
}
.footer-contact{
  display: flex;
  padding-bottom: 10px;
  align-items: flex-end;
}

@media screen and (max-width:1150px) {
  .home-text{
    max-width: 100%;
  }
  .home-image{
    max-width: 100%;
  }
  .home-title{
    margin-right: auto!important;
    margin-left: auto!important;
    margin-top: 0!important;
  }
  .home-subtitle{
    margin-left: 0px;
    text-align: center;
  }
  .home-title-text{
    margin-top: 20px!important;
  }

}

@media screen and (max-width:870px){
  .footer-sub-box2{
    align-items: baseline;
  }
  
  .footer-sub-box3{
    align-items: baseline;
  }
  .footer-box{
    width: 100%;
  }
  .footer-sub-box1{
    padding-bottom: 10px;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
